import chroma from "chroma-js"
import sizes from "../../assets/mediaQueries"

const getTextColor = (bgColor) =>
  chroma(bgColor).luminance() >= 0.35
    ? "rgba(0, 0, 0, 0.7)"
    : "rgb(255, 255, 255)"

export default {
  colorBox: {
    width: "20%",
    height: (props) => (props.showFullPalette ? "25%" : "50%"),
    margin: "0 auto",
    display: "inline-block",
    position: "relative",
    cursor: "pointer",
    marginBottom: "-6px",
    "&:hover button": {
      opacity: 1,
      transition: "0.5s"
    },
    [sizes.down("lg")]: {
      width: "25%",
      height: (props) => (props.showFullPalette ? "20%" : "33.33333%")
    },
    [sizes.down("md")]: {
      width: "50%",
      height: (props) => (props.showFullPalette ? "10%" : "20%")
    },
    [sizes.down("xs")]: {
      width: "100%",
      height: (props) => (props.showFullPalette ? "5%" : "10%")
    }
  },
  copyText: {
    color: (props) => getTextColor(props.bgColor)
  },
  colorName: {
    color: (props) => getTextColor(props.bgColor)
  },
  seeMore: {
    background: "rgba(255, 255, 255, 0.3)",
    position: "absolute",
    border: "none",
    right: "0px",
    bottom: "0px",
    width: "60px",
    height: "30px",
    textAlign: "center",
    lineHeight: "30px",
    textTransform: "uppercase",
    color: (props) => getTextColor(props.bgColor)
  },
  copyButton: {
    width: "100px",
    height: "30px",
    position: "absolute",
    display: "inline-block",
    top: "50%",
    left: "50%",
    marginLeft: "-50px",
    marginTop: "-15px",
    textAlign: "center",
    outline: "none",
    background: "rgba(255, 255, 255, 0.3)",
    fontSize: "1rem",
    lineHeight: "30px",
    textTransform: "uppercase",
    textDecoration: "none",
    border: "none",
    opacity: 0,
    color: (props) => getTextColor(props.bgColor)
  },
  boxContent: {
    position: "absolute",
    width: "100%",
    left: "0px",
    bottom: "0px",
    padding: "10px",
    color: "black",
    letterSpacing: "1px",
    textTransform: "uppercase",
    fontSize: "12px"
  },
  copyOverlay: {
    opacity: 0,
    zIndex: 0,
    width: "100%",
    height: "100%",
    transition: "transform 0.5s ease-in-out"
  },
  showOverlay: {
    opacity: 1,
    transform: "scale(50)",
    zIndex: "10",
    position: "absolute"
  },
  copyMsg: {
    position: "fixed",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "4rem",
    transform: "scale(0.1)",
    opacity: 0,
    color: "white",
    "& h1": {
      fontWeight: 400,
      textShadow: "1px 2px black",
      background: "rgba(255, 255, 255, 0.2)",
      width: "100%",
      textAlign: "center",
      margiBottom: 0,
      padding: "1rem",
      textTransform: "uppercase",
      [sizes.down("xs")]: {
        fontSize: "5rem"
      }
    },
    "& p": {
      fontSize: "2rem",
      fontWeight: 100
    }
  },
  showCopyMsg: {
    opacity: 1,
    transform: "scale(1)",
    zIndex: 10,
    transition: "all 0.3s ease-in-out",
    transitionDelay: "0.2s"
  }
}
